    --logo-animation-color-text: rgba(4,5,5,0.8);
    --trip-card-background-color:   #EEEEF1;
    --trip-card-background-border-color:  #F7F7FA;
    --trip-background-color:    #EEEEF1; 
    --trip-font-color: #28251F;
    --trip-destination-font-color: #28251F;
    --trip-card-border-color: #28251F; 
    --trip-h3-color: #FF8528;
    --trip-background-image-preview-color: #28251F;

    --form-background-color:  transparent;
    --form-input-border-color: #28251F;
    --form-font-color:#28251F;
    --form-input-background-color: #EDEDEC;
    --form-btn-border-color: #28251F;
    --form-btn-hover-color: #EDEDEC;
    --nav-user-color: #FF8528 ; 
    /* #FED99C tło do paska wyszukiwania */
    --main-search-primary-color: transparent;
    --main-search-background-color-mobile: #fff;
    --main-search-border-color: #28251F;
    --buttons-fill-color: #28251F ;
    --buttons-border-color: #28251F;
    --buttons-primary-color: #EDEDEC;;
    --buttons-font-color: #28251F;

    --hamburger-menu-primary-color:#28251F;
    --hamburger-menu-background-color: rgba(255,255,255,0.9);
    --header-primary-color:    #F9F9F9;
    --menu-overlay-color: #1d1716;

