/* Icons made by <a href="https://www.flaticon.com/authors/freepik" title="Freepik">Freepik</a> from <a href="https://www.flaticon.com/" title="Flaticon"> www.flaticon.com</a> */

body {
    width: 100vw;
    height: 100%;
    background-color: var(--home-background-color);
}

.header--wrapper {
    background-color: var(--header-background-color);
    margin: 0;
    padding:10px;
    position: relative;
    display: block;
}

.header {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: baseline;
    padding: 10px;
}

.hamburger {
    padding: 5px;
    background-color: transparent;
    border: 0;
    margin: 0;
    cursor: pointer;
    display: inline-block;
}

.hamburger__box {
    display: inline-block;
    width:20px;
    height: 24px;
    position: relative;
}

.hamburger__inner {
    display: inline-block;
    position: absolute;
    left:0;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    height: 3px;
    width: 100%;
    background-color: var(--hamburger-menu-primary-color);
    -webkit-transition: all 0.3s ease;
    transition: all 0.3s ease;
}

.hamburger__inner::before,
.hamburger__inner::after {
    content: '';
    display: inline-block;
    position: absolute;
    left:0;
    height: 3px;
    width: 100%;
    background-color: var(--hamburger-menu-primary-color);
}

.hamburger__inner::before {
     top: -6px;
}
.hamburger__inner::after {
    top: 6px;
}

.hamburger--active .hamburger__inner {
    -webkit-transform:rotate(-135deg);
            transform:rotate(-135deg);
}

.hamburger--active .hamburger__inner::before,
.hamburger--active .hamburger__inner::after {
    top: 0;
    -webkit-transform:rotate(90deg);
            transform:rotate(90deg);
}

.navigation {    
    display: none;
}

.navigation .navigation__user--logged {
    border-radius: 2px;
    background-color: var(--nav-user-color);
    font-weight: 800;
    color: white;
    padding: 0.5rem;    
}

.navigation--active {
    position: absolute;
    left:50%;
    top:170%;
    z-index:2;
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
    background-color: var(--hamburger-menu-background-color);
    width: 100vw;
    height: 200px;
    padding: 0.5rem;
    display: flex;
    justify-content: center;
    align-items: center;
}

.navigation--active ul {
    list-style-type: none;
    font-size: 1.2rem;
    line-height: 1.7rem;
    text-transform: lowercase;
}

.navigation--active ul li NavLink {
    text-decoration: none;
}

.link--logo {
    display: inline-block;
    color: var(--logo-primary-color);
    font-family: var(--logo-font-family);
    font-size: 2.2rem; 
    font-weight: 800;
    /* text-shadow: .03em .03em 0 var(--logo-shadow-color); */
    letter-spacing: 1px;
    cursor: pointer;
}

.link--logo a {
    text-transform: capitalize;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    font-size: 30px;
	letter-spacing:0.1em;
   -webkit-text-fill-color: transparent;
   -webkit-text-stroke-width: 1px;
   -webkit-text-stroke-color: white;
    text-shadow: 
		2px 2px #FF8528,
		5px 5px #28251F;
}

#main__searcher--desktop {
    width: 100vw;
    padding: 5px 15px;
    margin-bottom: 20px;
    background-color: var(--main-search-background-color-mobile);
    border-top: 2px solid var(--main-search-border-color);
    border-bottom: 2px solid var(--main-search-border-color);
    display: flex;
    justify-content: space-between;
}

#main__searcher__input {
    height: 1.8rem;
    width: 80%;
    border: none;
    border-radius: 5px;
    margin-right: 1rem;
    padding: 2rem;
    background-color: var(--main-search-background-color-mobile);
}

button.search__button {
    border: none;
    cursor: pointer;
    background-color: var(--main-search-background-color-mobile);
}

.icon__search {
    width: 30px;
    height: 30px;
    display: inline-block;
    background-image: url(/projekt-koncowy/build/static/media/search-1.772a434b.svg);
}

@media all and (min-width: 768px) {

    .header{
        justify-content: space-between;
        position: relative;
    }

    .header--wrapper {
        padding:30px;
    }
    .navigation ul li {
        color: var(--header-nav-color);
    }

    .hamburger__box {
        width: 40px;
        height: 40px;
    }

    .hamburger__inner::before {
        top: -10px;
    }
    .hamburger__inner::after {
       top: 10px;
    }

    .link--logo a {
        font-size: 5rem;
        letter-spacing: 7px;
        text-transform: capitalize;
        font-size: 80px;
        -webkit-text-stroke-width: 3px;
        text-shadow: 
            4px 4px #FF8528,
            10px 10px #28251F;
    }

    #main__searcher__input {
        width: 60%;
        font-size: 1.2rem;
        text-transform: lowercase;
    }
    #main__searcher--desktop {
        display: block;
        width: 100%;
        padding: 2rem;
        background-color: var(--main-search-primary-color);
        display: flex;
        justify-content: center;
        align-items: center;
    }
    button.search__button {
        background-color: transparent;
    }

}

@media all and (min-width: 1224px) {

    .header {
        height: 20%;
        padding: 1rem 2rem;
        justify-content: center;

    }
    .hamburger {
        display: none;
    }

    .navigation {
        display: block;
        margin: 1rem auto 0 auto;
        width: 60%;
        font-size: 1.2rem;
        font-weight: 400;
        padding-bottom: 2rem;
    }

    .navigation ul {
        display: flex;
        justify-content: space-evenly;
        align-items: center;
        list-style-type: none;
    }

    .navigation ul li {
        cursor: pointer;
        letter-spacing: 1px;
        font-size: 1.2rem;
        text-transform: uppercase;
    }

    .navigation ul li:hover {
        font-weight: 800;
    }

    .link--logo {
        font-size: 5rem;
        letter-spacing: 7px;
    }

    .link--logo::after {
        content: attr(data-letters);
        position: absolute;
        top: .06em; left: .06em;
        z-index: -1;
        text-shadow: none;
        background-image:
        linear-gradient(
          45deg,
          transparent 45%,
          rgb(29, 25, 18) 45%,
          rgb(49, 40, 27) 55%,
          transparent 0
          );
        background-size: .05em .05em;
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
    
        -webkit-animation: shad-anim 15s linear infinite;
    
                animation: shad-anim 15s linear infinite;
    }
    
    @-webkit-keyframes shad-anim {
        0% {background-position: 0 0}
        0% {background-position: 100% -100%}
    }
    
    @keyframes shad-anim {
        0% {background-position: 0 0}
        0% {background-position: 100% -100%}
    }

    #main__searcher__input {
        width: 30%;
    }
}
