}

.transport p {
    margin-top: 20px;
    margin-bottom: 10px;
    font-size: 0.8rem;
    font-weight: 700;
    text-transform: uppercase;
}

label[for=bus], label[for=car], label[for=airplane], label[for=train], label[for=ship],
label[for=hitchhiking], label[for=bike], label[for=other], label[for=onfoot] {
    position: relative;
    margin-left: 0;
    letter-spacing: 1px;
    font-size: 0.8rem;
    padding: 10px;
    padding-left: 40px;
    line-height: 30px;
}

label[for=bus]::before, label[for=car]::before, label[for=airplane]::before, label[for=train]::before, label[for=ship]::before,
label[for=hitchhiking]::before, label[for=bike]::before, label[for=other]::before, label[for=onfoot]::before {
    content: "";
    display: block;
    position: absolute;
    left: 0;
    z-index:-1;
    width: 30px;
    height: 30px;
    background-size: contain;
}

label[for=bus]::before {
    background-image: url(/projekt-koncowy/build/static/media/double-decker-bus.bd0874fd.svg);
}

label[for=car]::before {
    background-image: url(/projekt-koncowy/build/static/media/automotive.baac38bb.svg);
}

label[for=airplane]::before {
    background-image: url(/projekt-koncowy/build/static/media/plane.9bcbcf05.svg);
}

label[for=train]::before {
    background-image: url(/projekt-koncowy/build/static/media/train.77a77e21.svg);
}

label[for=ship]::before {
    background-image: url(/projekt-koncowy/build/static/media/ship.a4fa5905.svg);
}

label[for=hitchhiking]::before {
    background-image: url(/projekt-koncowy/build/static/media/hitchhiker.3956a23f.svg);
}

label[for=bike]::before {
    background-image: url(/projekt-koncowy/build/static/media/bike.aa1d8506.svg);
}

label[for=onfoot]::before {
    background-image: url(/projekt-koncowy/build/static/media/mountain.401396cb.svg);
}

label[for=other]::before {
    background-image: url(/projekt-koncowy/build/static/media/spaceship.ec8a494d.svg);
}

input[type=checkbox] {
    display: none;
}

input[type=checkbox] + label::after {
    content: "";
    display: block;
    position: absolute;
    box-sizing: border-box;
    left: 150px;
    top:25%;
    width: 30px;
    height: 30px;
    background-color:  var(--form-input-background-color);
    border: none;
    border-radius: 5px;
    cursor: pointer;
}

input[type=checkbox]:checked + label::after{
    background-image: url(/projekt-koncowy/build/static/media/close2.5d8fa210.svg);
    background-size: contain;
    background-clip: border-box;
    border: 8px solid var(--form-input-background-color);

}

